       </div>
     </div>
  </div>
</template>
<script>
  import "lib-flexible"
  import headers from './Headers.vue'
  export default {
    data() {
      return {
        title: "",
        fenxiang: false
      }
    },
    components: {
      headers
    },
    methods: {
      changef: function() {
        this.fenxiang = !this.fenxiang;
      }
    }
  }
</script>
