  .center>h4 {
    font-size: 48px;
    color: #000;
    text-align: center;
  }

  .introduce {
    font-size: 30px;
    color: #777777;
    text-align: center;
    padding-top: 84px;
    padding-bottom: 72px;
  }

  .guanzhu {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .guanzhu img {
    width: 58px;
    height: 58px;
  }

  .guanzhu span:nth-child(2) {
    font-size: 30px;
    margin-left: 32px;
  }

  .guanzhu span:nth-child(3) {
    height: 58px;
    line-height: 58px;
    font-size: 28px;
    margin-left: 96px;
    background: #ddbf7e;
    color: #fff;
    width: 162px;
    border-radius: 10px;
    text-align: center;
