    color: #aaa;
  }

  .footer p:first-child {
    font-size: 30px;
    color: #444;
    padding: 64px 40px 16px;
    line-height: 54px;
  }

  .footer p:last-child {
    /*border: solid 1px red;*/
    margin-top: 1rem;
  }

  .footer_table {
    height: 96px;
    line-height: 96px;
    display: flex;
  }

  .footer_table span:nth-child(1),
  .footer_table span:nth-child(2) {
