      background: #eee;
      position: absolute;
      bottom: 0;
      p {
        font-size: 24px;
        text-align: center;
        color: #4a4b55;
        height: 90px;
        line-height: 90px;
      }
      ul {
        overflow: hidden;
        width: 100%;
        padding-bottom: 50px;
        li {
          float: left;
          margin-left: 24px;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          img {
            width: 120px;
            height: 120px;
            border-radius: 0.4rem;
            background: #fff;
          }
          span {
            font-size: 22px;
            text-align: center;
            padding-top: 18px;
