<template>
    <header>
        <i></i>
        <img src="../assets/images/head.png" alt="">
       <p> {{title}}</p>
       <section>
         <img src="../assets/xqy/buy_icon_shopping_cart.png" alt="">
         <img src="../assets/xqy/buy_icon_share.png" alt="">
       </section>
    </header>
</template>

<style lang="less" scoped>
  header {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    height: 90px;
    background: #825E68;
    font-size: 34px;
    color: #fff;
    text-align: center;
    line-height: 90px;
    background: url('../assets/images/add-bg.png') no-repeat;
    background-size: 100% 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  i {
    position: absolute;
    width: 17px;
    height: 33px;
    background: url('../assets/images/returnGo.png') no-repeat left 22px center;
    background-size: 17px 33px;
    padding-left: 22px;
  }

  p {
    font-size: 28px;
    color: #fff;
    width: 142px;
    padding-left: 20px;
  }

  header>img {
    width: 58px;
    height: 58px;
    margin-left: 210px;
  }

  section{
    padding-left: 118px;
    img {
      width: 50px;
      height: 40px;
      margin-right: 30px;
    }
    img:last-child{
      width: 38px;
    }
  }
</style>

<script>
  export default {
    props: ['title'],
    methods: {
      returnGo() {
        window.history.go(-1)
      }
    }
  }
</script>



